import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Row, Col, Carousel } from 'antd';


const MolduraPort = styled.div`
width: 85%;
height: 20vh;
border-radius: 5px;
background: ${({ theme }) => theme.colors.white};
overflow: hidden;
margin-top: ${props => props.margintop};
margin-left: ${props => props.marginleft};
`
const Carouselcard = styled.div`
width: 90%;
height: 60vh;
margin-top: 10%;
border-radius: 5px;
margin-left: 10%;
overflow: hidden;
`
const Base = "../static/carrosel2.jpg"

export function Portfolio(props) {
    const [state, setState] = useState(false)
    const [photo, setPhoto] = useState("")



    return (
        <Row>
            <Col span={12}>
                <Row>
                    <Col span={8}>
                        <MolduraPort value={photo} marginleft="20%" margintop="15%" onClick={() => setPhoto("") && img.value.src} >
                            <img src={() => setState && "../static/carrosel1.jpg"} />
                        </MolduraPort>
                    </Col>
                    <Col span={8}>
                        <MolduraPort marginleft="15%" margintop="15%" onClick={() => setPhoto("../static/carrosel1.jpg")}>
                            <img src="../static/carrosel1.jpg" />
                        </MolduraPort>
                    </Col>
                    <Col span={8}>
                        <MolduraPort marginleft="10%" margintop="15%" onClick={() => setPhoto("../static/carrosel1.jpg")}>
                            <img src="../static/carrosel1.jpg" />
                        </MolduraPort>
                    </Col>
                </Row>
                <Row>
                    <Col span={8}>
                        <MolduraPort marginleft="20%" margintop="10%" onClick={() => setPhoto("../static/carrosel1.jpg")}>
                            <img src="../static/carrosel1.jpg" />
                        </MolduraPort>
                    </Col>
                    <Col span={8}>
                        <MolduraPort marginleft="15%" margintop="10%" onClick={() => setPhoto("../static/carrosel1.jpg")}>
                            <img src="../static/carrosel1.jpg" />
                        </MolduraPort>
                    </Col>
                    <Col span={8}>
                        <MolduraPort marginleft="10%" margintop="10%" onClick={() => setPhoto("../static/carrosel1.jpg")}>
                            <img src="../static/carrosel1.jpg" />
                        </MolduraPort>
                    </Col>
                </Row>
            </Col>
            <Col span={10}>
                <Carouselcard >
                   <img src={photo || Base} />
                </Carouselcard>
            </Col>
        </Row>
    )
}